import React from 'react';
import { GoogleLogin } from '@react-oauth/google';
import jwt_decode from 'jwt-decode';  // Default import for decoding JWT
import { useNavigate } from 'react-router-dom';

const GoogleAuth = () => {
  const navigate = useNavigate();

  const handleSuccess = (credentialResponse) => {
    try {
      // Decode the JWT token received from Google
      const decodedToken = jwt_decode(credentialResponse.credential); // Decode the credential
      console.log('Login Success:', decodedToken);

      // Optionally, store user information in localStorage
      localStorage.setItem('googleUser', JSON.stringify(decodedToken));

      // Navigate to home or another route
      navigate('/');
    } catch (error) {
      console.error('Error decoding token:', error);
    }
  };

  const handleError = () => {
    console.error('Login Failed');
  };

  return (
    <div style={{ textAlign: 'center', marginTop: '50px' }}>
      <h2>Login with Google</h2>
      <GoogleLogin
        onSuccess={handleSuccess}  // Handle success
        onError={handleError}  // Handle failure
        useOneTap  // Optional: allows one-tap login
      />
    </div>
  );
};

export default GoogleAuth;
